
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        
















































































































































































































































































































































































































































$C2C-orange: red;

::v-deep {
  .elevation-profile-chart {
    height: 300px; // default height on page

    .axis path,
    .axis line {
      fill: none;
      stroke: black;
      shape-rendering: crispEdges;
    }

    .line {
      fill: none;
      stroke: $C2C-orange;
      stroke-width: 3px;
    }

    .circle {
      fill: white;
      stroke: $C2C-orange;
      stroke-width: 2px;
    }

    line.target {
      stroke: lightgray;
      stroke-width: 1px;
    }

    .bubble {
      font-size: 12px;
    }

    .overlay {
      fill: none;
      pointer-events: all;
    }
  }
}

:fullscreen .elevation-profile-container {
  position: absolute;
  bottom: 0;
  height: 30%;
  max-height: 350px;
  min-height: 200px;
  width: 100%;
  background-color: white;

  > form {
    position: absolute;
    right: 0;
    z-index: 2;
  }

  .elevation-profile-chart {
    height: 100%;

    ::v-deep svg {
      position: absolute;
    }
  }
}
